.dialogs {
    display: grid;
    grid-template-columns: 2fr 8fr;
    width: 100%;
    padding: 1rem 2rem;
}

.dialogs__messages {
    padding: 0;
    list-style-type: none;
}

.dialogs__add {
    padding-top: 1rem;
    width: 100%;
    max-width: 220px;
}

.dialogs__text {
    display: block;
    width: 100%;
    resize: none;
    padding-top: 10px;
    line-height: 0.7;
}